import { useEffect } from 'react';
import { AuthProvider } from '../context/AuthContext'; // Adjust the path if necessary
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js'; // Include Bootstrap JavaScript
import '../styles/home.css'; // Ensure your custom styles are imported after Bootstrap

function MyApp({ Component, pageProps }) {
  useEffect(() => {
    // This ensures that Bootstrap's JavaScript is correctly loaded
    import('bootstrap/dist/js/bootstrap.bundle.min.js');
  }, []);

  return (
    <AuthProvider>
      <Component {...pageProps} />
    </AuthProvider>
  );
}

export default MyApp;